import { LitElement, css, html } from 'lit';
import { customElement, property } from 'lit/decorators.js';

import '@shoelace-style/shoelace/dist/components/radio-group/radio-group.js';
import '@shoelace-style/shoelace/dist/components/radio-button/radio-button.js';

@customElement('food-type-form')
export class FoodTypeFormComponent extends LitElement {
  static get styles() {
    return [
      css`
        sl-radio-group::part(form-control) {
          display: flex;
          justify-content: center;
        }
      `,
    ];
  }

  @property() type: 'food' | 'dish';

  TypeChanged(e: any) {
    this.dispatchEvent(
      new CustomEvent('type-changed', { detail: e.target.value })
    );
  }

  render() {
    return html`
      <sl-radio-group
        size="large"
        name="a"
        @sl-change=${this.TypeChanged}
        .value="${this.type}"
      >
        <sl-radio-button value="food">
          <span slot="prefix">🥚🍗</span>
          Food
          <span slot="suffix">🥚🥩</span>
        </sl-radio-button>

        <sl-radio-button value="dish">
          <span slot="prefix">🌯🥗</span>
          Dish
          <span slot="suffix">🍝🍛</span>
        </sl-radio-button>
      </sl-radio-group>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'food-type-form': FoodTypeFormComponent;
  }
}
