import { ClientSideRowModelModule } from '@ag-grid-community/client-side-row-model';
import {
  CellClickedEvent,
  ColDef,
  GridApi,
  GridOptions,
  ValueGetterParams,
  createGrid,
} from '@ag-grid-community/core';
import { LitElement, PropertyValues, css, html } from 'lit';
import { customElement, property, queryAsync } from 'lit/decorators.js';
import { Food, isDish } from '@models/diet/food-item';
import { resolve } from '@services/utils';

import '@shoelace-style/shoelace/dist/components/animation/animation.js';

@customElement('food-items-grid')
export class FoodItemsGridComponent extends LitElement {
  static get styles() {
    return [
      css`
        .ag-theme-alpine,
        .ag-theme-alpine-dark,
        .ag-theme-alpine-auto-dark {
          // --ag-grid-size: 2px;
          --ag-cell-horizontal-padding: 4px;
          // --ag-header-height:2px;
          // --ag-line-height: 2px;
          // --ag-row-height: 2px;
          // --ag-list-item-height: 10px;
          // --ag-font-size: 10px;
          height: 100%;
          width: 100%;
        }
        .ag-header-group-cell {
          text-align: center;
        }
      `,
    ];
  }

  @property() foods: Food[];

  @queryAsync('#myGrid') myGridDiv: Promise<HTMLElement>;

  constructor() {
    super();
    this._SetupGrid();
  }

  willUpdate(changedProperties: PropertyValues<this>) {
    if (changedProperties.has('foods')) {
      this._UpdateGrid();
    }
  }

  private _UpdateGrid() {
    this._gridApi?.setGridOption('rowData', this.foods);
    // this._gridApi?.setGridOption('rowHeight', 18);
  }

  private _onFoodClicked(e: CellClickedEvent) {
    this.dispatchEvent(
      new CustomEvent('food-clicked', {
        bubbles: true,
        detail: { food: e.data, field: e.colDef.headerName },
      })
    );
  }

  // Grid API: Access to Grid API methods
  private _gridApi: null | GridApi = null;

  private _columnDefs: ColDef[] = [
    {
      headerName: 'Name',
      field: 'Name',
      onCellClicked: (e) => this._onFoodClicked(e),
      valueGetter: (p) => p.data.Name,
      pinned: 'left',
      minWidth: 100,
      maxWidth: 100,
      cellStyle: { textAlign: 'right' },
    },
    {
      headerName: 'Today',
      onCellClicked: (e) => this._onFoodClicked(e),
      field: 'Today',
      pinned: 'left',
      valueGetter: (p) => p.data.Today ?? 0,
    },
    {
      headerName: 'Diem',
      field: 'Diem',
      onCellClicked: (e) => this._onFoodClicked(e),
      valueGetter: (p) => p.data.Diem ?? 0,
    },
    { headerName: 'Cal.', field: 'Macros.Calories' },
    { headerName: 'Pro.', field: 'Macros.Protein' },
    { headerName: 'Carb.', field: 'Macros.Carbs' },
    { headerName: 'Fats', field: 'Macros.Fat' },
  ];

  private _gridOptions: GridOptions<Food> = {
    defaultColDef: {
      maxWidth: 80,
      minWidth: 60,
      onCellClicked: (e) => this._onFoodClicked(e),
      valueGetter: this._valueGetter,
      cellStyle: { textAlign: 'center' },
    },

    columnDefs: this._columnDefs,
    onGridReady: (e) => e.api.sizeColumnsToFit(),
  };

  private _valueGetter(params: ValueGetterParams<Food, any>) {
    if (!params?.data && !params.colDef.field) return '';
    const food = params!.data!;
    const field = params!.colDef!.field!;
    if (isDish(food)) {
      return food.Ingredients.reduce(
        (acc, val) => acc + Number(resolve(val, field)),
        0
      );
    } else {
      return Number(resolve(food, field));
    }
  }

  private _SetupGrid() {
    this.myGridDiv.then((e) => {
      this._gridApi = createGrid(e!, this._gridOptions, {
        modules: [ClientSideRowModelModule],
      });
    });
  }

  render() {
    return html`
      <link
        rel="stylesheet"
        href="https://cdn.jsdelivr.net/npm/ag-grid-community@31.2.1/styles/ag-grid.min.css"
      />
      <link
        rel="stylesheet"
        href="https://cdn.jsdelivr.net/npm/ag-grid-community@31.2.1/styles/ag-theme-alpine.min.css"
      />

      <section style="height: 100%; width: 98vw;">
        <div id="myGrid" class="ag-theme-alpine-auto-dark"></div>
      </section>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'food-items-grid': FoodItemsGridComponent;
  }
}
