import { LitElement, html } from 'lit';
import { customElement, query, state } from 'lit/decorators.js';

// You can also import styles from another file
// if you prefer to keep your CSS seperate from your component
import { styles } from './diet-styles';

import { styles as sharedStyles } from '../../styles/shared-styles';

import '@components/diet/totals/diet-totals.component';
import '@components/diet/profile/diet-profile.component';
import '@components/diet/food-items/food-items-grid.component';
import '@components/diet/food-items/food-form.component';

import '@shoelace-style/shoelace/dist/components/split-panel/split-panel.js';
import '@shoelace-style/shoelace/dist/components/drawer/drawer.js';
import '@shoelace-style/shoelace/dist/components/button/button.js';
import SlButton from '@shoelace-style/shoelace/dist/components/button/button.js';
import SlDrawer from '@shoelace-style/shoelace/dist/components/drawer/drawer.js';

import { Food, FoodItem } from '@models/diet/food-item';
import { classMap } from 'lit/directives/class-map.js';
import { GetFoods, SaveFood } from '@services/food-items.service';

@customElement('diet-page')
export class DietPage extends LitElement {
  static styles = [sharedStyles, styles];

  @query('sl-drawer') drawer: SlDrawer;
  @query('#add-food') addFoodBtn: SlButton;
  @state() private foods: Food[] = [];
  @state() private activeFood: Food | null = null;
  @state() private _drawerSize: string = '25rem';
  @state() private _creatingNew = false;
  @state() private fullForm = false;
  slDrawerClassMap = { hideOverlay: false };

  async firstUpdated() {
    await this.LoadFoods();
  }

  private async LoadFoods() {
    this.foods = await GetFoods();
  }

  private ShowFood(e: CustomEvent<any>) {
    this.activeFood = e.detail.food ? e.detail.food : new FoodItem();
    this._creatingNew = (this.activeFood?.Name?.length ?? 0) === 0;
    this.fullForm = this._creatingNew;

    this.fullForm ? this.ExpandDrawer() : this.CondenseDrawer();

    this.drawer.show();
  }

  async SaveFood(e: CustomEvent<Food>){
    await SaveFood(e.detail);
    await this.LoadFoods();
  }

  ExpandDrawer() {
    this.fullForm = true;
    this._drawerSize='50rem;'
    this.slDrawerClassMap.hideOverlay = false;
  }

  CondenseDrawer() {
    this.fullForm = false;
    this._drawerSize='20rem;'
    this.slDrawerClassMap.hideOverlay = true;
  }

  CloseDrawer() {
    this.CondenseDrawer()
    this.drawer.hide()
  }

  render() {
    return html`
      <main>
        <sl-split-panel vertical position="100">
          <div slot="divider" class="divider" id="divider">
            <sl-icon name="grip-horizontal"> </sl-icon>
            <span class="divider-label"> 🍗 🍕 Foonds 🍅 🍔 </span>
            <sl-icon name="grip-horizontal"> </sl-icon>
          </div>

          <div slot="start">
            <div class="panel">
              <h2>Diet</h2>
              <diet-totals .foods=${this.foods}></diet-totals>
              <diet-profile style="width:98vw"></diet-profile>
            </div>
          </div>

          <div slot="end">
            <div class="panel" id="food-panel">
              <food-items-grid style="height:85%"
                .foods=${this.foods}
                @food-clicked="${this.ShowFood}"
              ></food-items-grid>
                <sl-button size="large" id="add-food"
                @click="${this.ShowFood}"
                >+ Add Food</sl-button>
              </div>
            </div>
          </div>
        </sl-split-panel>

        <sl-drawer placement="bottom" no-header
        style="--size: ${this._drawerSize}"
        class="${classMap(this.slDrawerClassMap)}">
          <food-form .food="${this.activeFood}"
          .fullForm=${this.fullForm}
          .creatingNew=${this._creatingNew}
          @food-changed=${this.SaveFood}
          @on-close-food-form=${this.CloseDrawer}
          @on-full-form=${this.ExpandDrawer}
          @on-condensed=${this.CondenseDrawer}
          ></food-form>
        </sl-drawer>
      </main>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'diet-page': DietPage;
  }
}

