import { PersonalInfo } from '@models/personal-info';
import { LitElement, css, html } from 'lit';
import { customElement, state } from 'lit/decorators.js';

import '@shoelace-style/shoelace/dist/components/select/select.js';
import '@shoelace-style/shoelace/dist/components/option/option.js';
import '@shoelace-style/shoelace/dist/components/input/input.js';

import { GetPersonalInfo, SavePersonalInfo } from '@services/personal-info.service';

export type ComponentEvents = 'profile-changed';

@customElement('personal-profile-form')
export class PersonalProfileFormComponent extends LitElement {

  static styles = css`
  sl-input {
    min-width: 0;
    flex: 1 1 auto;
  }
  `

  @state() private _profile: PersonalInfo;

  constructor() {
    super();
  }

  async firstUpdated() {
    this._profile = await GetPersonalInfo();
    await new Promise((r) => setTimeout(r, 0));
    this.addEventListener('profile-changed', async (e) => {
      await SavePersonalInfo((<CustomEvent>e).detail);
    });
  }

  private NotifyChange() {
    this.dispatchEvent(
      new CustomEvent('profile-changed', {
        bubbles: true,
        composed: true,
        detail: this._profile,
      })
    );
  }

  private ProfileChanged(e: {target: HTMLInputElement}, key: keyof PersonalInfo) {
    this._profile = {...this._profile, [key]: e.target.value};
    this.NotifyChange();
  }


  render() {
    return !this._profile
      ? html``
      : html`
      <form class="input-validation-required">
        <sl-input
          id="nameInput"
          .value="${this._profile.Name}"
          @sl-input="${(e: {target: HTMLInputElement})=>this.ProfileChanged(e, 'Name')}"
          name="Name"
          label="Name"
          required
        ></sl-input>
        <br />
        <div style="display: flex; gap: 1rem;">
          <sl-select
            id="genderSelect"
            .value="${this._profile.Gender}"
            @sl-change="${(e: {target: HTMLInputElement})=>this.ProfileChanged(e, 'Gender')}"
            label="Gender"
            required
          >
            <sl-option value="Male">Male</sl-option>
            <sl-option value="Female">Female</sl-option>
          </sl-select>
          <sl-input
            id="ageInput"
            .value="${this._profile.Age.toString()}"
            @sl-input="${(e: {target: HTMLInputElement})=>this.ProfileChanged(e, 'Age')}"
            name="Age"
            label="Age"
            type="number"
            required
          ></sl-input>
        </div>
        <br />
        <div style="display: flex; gap: 1rem;">
          <sl-input
            id="heightInput"
            .value="${this._profile.Height.toString()}"
            @sl-input="${(e: {target: HTMLInputElement})=>this.ProfileChanged(e, 'Height')}"
            name="Height"
            label="Height"
            required
          ></sl-input>
          <sl-input
            id="weightInput"
            .value="${this._profile.Weight.toString()}"
            @sl-input="${(e: {target: HTMLInputElement})=>this.ProfileChanged(e, 'Weight')}"
            name="Weight"
            label="Weight"
            type="number"
            required
          ></sl-input>
        </div>
      </form>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    "personal-profile-form": PersonalProfileFormComponent;
  }
}