import { Dish } from "@models/diet/food-item";
import { LitElement, PropertyValues, css, html } from "lit";
import { customElement, property, state } from "lit/decorators.js";

import '@shoelace-style/shoelace/dist/components/button/button.js';
import '@shoelace-style/shoelace/dist/components/input/input.js';

@customElement('dish-form')
export class DishForm extends LitElement {
  static get styles() {
    return [css``];
  }

  @property() food: Dish | null;
  @state() private _foodForm: Dish | null;

  willUpdate(changedProperties: PropertyValues<this>) {
    if (changedProperties.has('food')) {
      this._foodForm = structuredClone(this.food);
    }
  }

  render() {
    return html`
      ${this._foodForm?.Ingredients.map(x=>x.Name)}
    `;
  }

}

declare global {
    interface HTMLElementTagNameMap {
      'dish-form': DishForm;
    }
  }
