import { FoodItem } from '@models/diet/food-item';
import { Macros } from '@models/diet/macros';
import { macrosList } from '@models/diet/types';
import { LitElement, PropertyValues, css, html } from 'lit';
import { customElement, property, state } from 'lit/decorators.js';
import { UpEvent } from '../../../domain/html/UpEvent';

@customElement('food-item-form')
export class FoodItemForm extends LitElement {
  static get styles() {
    return [
      css`
        .macros {
          display: flex;
          flex-wrap: wrap;
          gap: 1rem;
          justify-content: space-evenly;
        }

        sl-input {
          min-width: 0;
          flex: 0 1 45%;
        }
      `,
    ];
  }

  @property() food: FoodItem | null;
  @state() private _foodForm: FoodItem | null;

  willUpdate(changedProperties: PropertyValues<this>) {
    if (changedProperties.has('food')) {
      this._foodForm = structuredClone(this.food);
    }
  }

  MacrosChanged = (key: keyof Macros) => (e: { target: HTMLInputElement }) => {
    this._foodForm = {
      ...this._foodForm,
      Macros: { ...this._foodForm?.Macros, [key]: Number(e.target.value) },
    } as FoodItem;
    this.dispatchEvent(UpEvent('macro-changed', this._foodForm));
  };

  private input = (field: keyof Macros) => html`
    <sl-input
      id="${field}Input"
      .value="${(this._foodForm?.Macros[field] ?? 0).toString()}"
      @sl-change="${this.MacrosChanged(field)}"
      .name="${field}"
      .label="${field}"
      type="number"
      required
    >
      <span slot="suffix">${field == 'Calories' ? 'kcal' : 'g'}</span>
    </sl-input>
  `;

  render() {
    return html` <div class="macros">${macrosList.map(this.input)}</div> `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'food-item-form': FoodItemForm;
  }
}
