import { LitElement, css, html } from 'lit';
import { customElement, property } from 'lit/decorators.js';

import '@shoelace-style/shoelace/dist/components/button/button.js';
import '@shoelace-style/shoelace/dist/components/input/input.js';
import { UpEvent } from '../../../domain/html/UpEvent';

@customElement('quantity-form')
export class  QuantityForm extends LitElement {
  static get styles() {
    return [css`
      :host{
        display: flex;
        gap: .25rem;
        width: 100%;
        align-items: center;
      }
      sl-button{
        max-width:3rem;
      }
      sl-input{
        min-width: 0;
        flex: 0 1 18%;
      }
      sl-input::part(input){
        padding: 0;
        text-align: center;
      }
    `];
  }

  @property() quantity: number = 0;

  private notify(){
    this.dispatchEvent(UpEvent('quantity-changed', this.quantity));
  }
  private QuantityChanged(e: { target: HTMLInputElement }) {
    this.quantity = Number(e.target.value);
    this.notify();
  }
  private ChangeQuantity(amount: number){
    this.quantity += amount;
    this.notify();
  }

  render() {
    return html`
      <sl-button @click=${()=>this.ChangeQuantity(-1)} variant="primary" size="large">- 1</sl-button>
      <sl-button @click=${()=>this.ChangeQuantity(-.5)} variant="primary" size="medium">- 0.5</sl-button>
      <sl-button @click=${()=>this.ChangeQuantity(-.1)} variant="primary" size="small">- 0.1</sl-button>
      <sl-input
        id="quantityInput"
        .value="${this.quantity.toString()}"
        @sl-change="${this.QuantityChanged}"
        name="Quantity"
        type="number"
        required
      ></sl-input>
      <sl-button @click=${()=>this.ChangeQuantity(+.1)} variant="primary" size="small">+ 0.1</sl-button>
      <sl-button @click=${()=>this.ChangeQuantity(+.5)} variant="primary" size="medium">+ 0.5</sl-button>
      <sl-button @click=${()=>this.ChangeQuantity(+1)} variant="primary" size="large">+ 1</sl-button>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'quantity-form': QuantityForm;
  }
}
