import { LitElement, html } from 'lit';
import { customElement, state } from 'lit/decorators.js';
import { DietGoals } from '@models/diet/diet-goals';
import { GetDietGoals, SaveDietGoals } from '@services/diet-goals.service';

import '@shoelace-style/shoelace/dist/components/select/select.js';
import '@shoelace-style/shoelace/dist/components/option/option.js';
import '@shoelace-style/shoelace/dist/components/input/input.js';
import '@shoelace-style/shoelace/dist/components/range/range.js';
import { activityLevelList, weightGoalList } from '@models/diet/types';

@customElement('diet-profile-form')
export class DietProfileFormComponent extends LitElement {
  @state() private _goals: DietGoals;

  constructor() {
    super();
  }

  async firstUpdated() {
    this._goals = await GetDietGoals();
    this.addEventListener('diet-profile-changed', async (e) => {
      await SaveDietGoals((<CustomEvent>e).detail);
    });
  }

  private NotifyChange() {
    this.dispatchEvent(
      new CustomEvent('diet-profile-changed', {
        bubbles: true,
        composed: true,
        detail: this._goals,
      })
    );
  }

  GoalsChanged(e: { target: HTMLInputElement }, key: keyof DietGoals) {
    this._goals = { ...this._goals, [key]: e.target.value };
    this.NotifyChange();
  }

  private getTextDisplayText = (value: string) => value.replace('_', ' ');

  private getOptions = (values: string[]) =>
    values.map(
      (x) =>
        html`<sl-option value="${x}">${this.getTextDisplayText(x)}</sl-option>`
    );

  render() {
    return !this._goals
      ? html``
      : html`
          <form class="input-validation-required">
            <br />
            <sl-select
              id="activityLevelSelect"
              label="Activity Level"
              .value="${this._goals.ActivityLevel}"
              @sl-change="${(e: { target: HTMLInputElement }) =>
                this.GoalsChanged(e, 'ActivityLevel')}"
              required
            >
              ${this.getOptions(activityLevelList)}
            </sl-select>
            <br />
            <sl-select
              id="weightGoalSelect"
              .value="${this._goals.WeightGoal}"
              @sl-change="${(e: { target: HTMLInputElement }) =>
                this.GoalsChanged(e, 'WeightGoal')}"
              label="Goal"
              required
            >
              ${this.getOptions(weightGoalList)}
            </sl-select>
            <br />
            <sl-input
              id="fatCarbSplitInput"
              .value="${this._goals.FatCarbSplit.toString()}"
              @sl-change="${(e: { target: HTMLInputElement }) =>
                this.GoalsChanged(e, 'FatCarbSplit')}"
              name="FatCarbSplit"
              min="0"
              max="100"
              type="number"
              label="Fat / Carb Split"
              required
            >
              <span slot="suffix">% Fat</span>
            </sl-input>

            <br />
            <sl-range
              tooltip="none"
              id="fatCarbSplitRange"
              @sl-input="${(e: { target: HTMLInputElement }) =>
                this.GoalsChanged(e, 'FatCarbSplit')}"
              .value="${this._goals.FatCarbSplit}"
              name="FatCarbSplit"
              min="0"
              max="100"
            ></sl-range>
            <br />
          </form>
        `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'diet-profile-form': DietProfileFormComponent;
  }
}
