import { ClientSideRowModelModule } from '@ag-grid-community/client-side-row-model';
import {
  ColDef,
  GridApi,
  GridOptions,
  ValueFormatterParams,
  createGrid,
} from '@ag-grid-community/core';
import { DietGoals } from '@models/diet/diet-goals';
import { PersonalInfo } from '@models/personal-info';
import { LitElement, PropertyValues, css, html } from 'lit';
import { customElement, property, queryAsync, state } from 'lit/decorators.js';
import { CalculatePersonal } from '@services/calculate-macros.service';
import { GetDietGoals } from '@services/diet-goals.service';
import { GetPersonalInfo } from '@services/personal-info.service';
import { TotalsTable } from '@models/diet/diet-daily-totals';
import { Food, isDish } from '@models/diet/food-item';
import { Macro } from '@models/diet/types';

@customElement('diet-totals')
export class DietTotalsComponent extends LitElement {
  static get styles() {
    return [
      css`
        // .ag-theme-alpine, .ag-theme-alpine-dark, .ag-theme-alpine-auto-dark {
        //   --ag-grid-size: 3px;
        //   --ag-list-item-height: 20px;
        //   --ag-font-size: 10px;
        // }
      `,
    ];
  }
  @property() foods: Food[];
  @state() private _profile: PersonalInfo;
  @state() private _diet: DietGoals;

  private _rendered = false;

  @queryAsync('#myGrid') myGridDiv: Promise<HTMLElement>;

  constructor() {
    super();
    this._SetupGrid();
    this._SetupGoalListeners();
    this._SetupDiemListeners();
    this._SetupTodayListeners();
  }

  async firstUpdated() {
    const [diet, profile] = await Promise.all([
      GetDietGoals(),
      GetPersonalInfo(),
    ]);
    this._diet = diet;
    this._profile = profile;
    this._UpdateGoal();
    this._rendered = true;
  }
  willUpdate(changedProperties: PropertyValues<this>) {
    if (this._rendered && changedProperties.has('foods')) {
      this._UpdateGoal();
    }
  }

  private _SetupTodayListeners() {}

  private _SetupDiemListeners() {}

  private _SetupGoalListeners() {
    window.addEventListener('profile-changed', (e) => {
      this._profile = (<CustomEvent>e).detail;
      this._UpdateGoal();
    });

    window.addEventListener('diet-profile-changed', (e) => {
      this._diet = (<CustomEvent>e).detail;
      this._UpdateGoal();
    });
  }

  private _UpdateGoal() {
    var macros = CalculatePersonal(this._profile, this._diet);
    this._rowData = this._rowData.map((x) => ({
      ...x,
      Goal: macros[x.Macro],
      Diem: this.calculateMacrosFoods(this.foods, x.Macro, 'Diem'),
      Today: this.calculateMacrosFoods(this.foods, x.Macro, 'Today'),
    }));
    this._gridApi?.setGridOption('rowData', this._rowData);
  }

  calculateMacrosFoods(
    foods: Food[],
    macro: Macro,
    multiplierKey: 'Today' | 'Diem'
  ) {
    return foods.reduce((acc, val) => {
      if (isDish(val)) {
        return (
          acc +
          val.Ingredients.reduce(
            (acc2, ingredient) =>
              ingredient.Macros[macro] * Number(ingredient[multiplierKey]) +
              acc2,
            0
          ) *
            Number(val[multiplierKey])
        );
      } else {
        return acc + val.Macros[macro] * Number(val[multiplierKey]);
      }
    }, 0);
  }

  // Grid API: Access to Grid API methods
  private _gridApi: null | GridApi = null;

  private _columnDefs: ColDef[] = [
    { field: 'Macro' },
    { field: 'Today', valueFormatter: this._numberFormatter },
    { field: 'Diem', valueFormatter: this._numberFormatter },
    { field: 'Goal', valueFormatter: this._numberFormatter },
  ];

  @state() private _rowData: TotalsTable[] = [
    { Macro: 'Calories', Today: 2767, Diem: 2790, Goal: 2800 },
    { Macro: 'Protein', Today: 180, Diem: 170, Goal: 165 },
    { Macro: 'Carbs', Today: 300, Diem: 280, Goal: 250 },
    { Macro: 'Fat', Today: 80, Diem: 65, Goal: 70 },
  ];

  private _gridOptions: GridOptions<TotalsTable> = {
    columnDefs: this._columnDefs,
    onGridReady: (e) => e.api.sizeColumnsToFit(),
  };

  private _numberFormatter(p: ValueFormatterParams<any, any>) {
    return Math.round(p.data![p.colDef?.field!]).toString();
  }

  private _SetupGrid() {
    this.myGridDiv.then((e) => {
      this._gridApi = createGrid(e!, this._gridOptions, {
        modules: [ClientSideRowModelModule],
      });
    });
  }

  render() {
    return html`
      <link
        rel="stylesheet"
        href="https://cdn.jsdelivr.net/npm/ag-grid-community@31.2.1/styles/ag-grid.css"
      />
      <link
        rel="stylesheet"
        href="https://cdn.jsdelivr.net/npm/ag-grid-community@31.2.1/styles/ag-theme-alpine.min.css"
      />

      <div style="height: 230px; width: 98vw;">
        <div
          id="myGrid"
          class="ag-theme-alpine-auto-dark"
          style="height: 100%; width: 100%"
        ></div>
      </div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'diet-totals': DietTotalsComponent;
  }
}
