import { LitElement, css, html, nothing } from 'lit';
import { customElement, property } from 'lit/decorators.js';
import { Food, isDish } from '@models/diet/food-item';

import '@shoelace-style/shoelace/dist/components/button/button.js';
import '@shoelace-style/shoelace/dist/components/input/input.js';
import '@shoelace-style/shoelace/dist/components/select/select.js';
import '@shoelace-style/shoelace/dist/components/option/option.js';

import { UpEvent } from '../../../domain/html/UpEvent';
import { portionMeasureList } from '@models/diet/types';

@customElement('food-title-form')
export class FoodTitleFormComponent extends LitElement {
  static get styles() {
    return [
      css`
        :host {
          display: flex;
          justify-content: space-between;
          align-items: start;
        }
        h2, #portionSelect, .blank {
            margin: var(--sl-spacing-x-small) 0;
        }
        .blank{
            height: var(--sl-input-height-medium);
        }
        .food-name {
          display: flex;
          align-items: center;
          gap: 1rem;
        }
        .food-header {
          display: flex;
          flex-direction: column;
        }
      `,
    ];
  }

  @property({ attribute: false }) food: Food | null;
  @property({ type: Boolean }) showClose: boolean = true;
  @property({ type: Boolean }) isEditing: boolean = true;
  @property({ type: Boolean }) showEditButton: boolean = true;

  private getTextDisplayText = (value: string) => value.replace('_', ' ');

  private getOptions = (values: string[]) =>
    values.map(
      (x) =>
        html`<sl-option value="${x}">${this.getTextDisplayText(x)}</sl-option>`
    );

  private _dispatch = (type: string, payload?: any) =>
    this.dispatchEvent(UpEvent(type, payload));

  private NameChanged = (e: { target: HTMLInputElement }) =>
    this._dispatch('food-name-changed', e.target.value);

  private PortionChanged = (e: { target: HTMLInputElement }) =>
    this._dispatch('portion-changed', e.target.value);

  private EditClicked = () =>
    this._dispatch(this.isEditing ? 'on-condensed' : 'on-full-form');

  private CloseClicked = () => this._dispatch('on-close-food-form');

  render() {
    const title = this.isEditing
      ? html`<sl-input
          id="foodName"
          name="food-name"
          placeholder="Name"
          required
          .value="${this.food?.Name ?? ''}"
          @sl-change="${this.NameChanged}"
        ></sl-input>`
      : html`<h2>${this.food?.Name}</h2>`;

    const portion =
      this.food && isDish(this.food)
        ? html`<div class="blank"></div>`
        : this.isEditing
        ? html`
            <sl-select
              id="portionSelect"
              .value="${this.food?.Portion?.toString() ?? ''}"
              placeholder="Portion"
              @sl-change="${this.PortionChanged}"
              required
            >
              ${this.getOptions(portionMeasureList)}
            </sl-select>
          `
        : html`<span font-size: var(--sl-font-size-small)
            >${this.food?.Portion}</span
          >`;

    const editButton = this.showEditButton
      ? html`
          <sl-button variant="text" circle>
            <sl-icon
              @click=${this.EditClicked}
              name="${this.isEditing ? 'check-lg' : 'pencil'}"
              label="Edit"
            ></sl-icon>
          </sl-button>
        `
      : nothing;

    const closeButton = !this.showClose
      ? nothing
      : html`
          <sl-button variant="text" circle>
            <sl-icon
              @click=${this.CloseClicked}
              name="x-lg"
              label="Close"
            ></sl-icon>
          </sl-button>
        `;

    return html`
      <div class="food-header">
        <div class="food-name">${title} ${editButton}</div>
        ${portion}
      </div>
      ${closeButton}
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'food-title-form': FoodTitleFormComponent;
  }
}
