import { LitElement, PropertyValues, css, html, nothing } from 'lit';
import { customElement, property, state } from 'lit/decorators.js';
import { Dish, Food, FoodItem, isDish } from '@models/diet/food-item';

import './dish.form';
import './food-item.form';
import './food-type.form';
import './food-title.form';
import './quantity.form';

import '@shoelace-style/shoelace/dist/components/button/button.js';
import '@shoelace-style/shoelace/dist/components/input/input.js';
import { UpEvent } from '../../../domain/html/UpEvent';

@customElement('food-form')
export class FoodFormComponent extends LitElement {
  static get styles() {
    return [
      css`
        form {
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
        }
        .form-body {
          display: flex;
          flex-direction: column;
          gap: 1rem;
        }
        #save {
          width: 100%;
        }
      `,
    ];
  }

  @property({ attribute: false }) food: Food | null;
  @property({ type: Boolean }) fullForm: boolean;
  @property({ type: Boolean }) creatingNew: boolean;

  @state() private _type: 'food' | 'dish';
  @state() private _foodForm: Food | null;
  @state() private _isFormValid: boolean = false;

  constructor() {
    super();
  }

  async firstUpdated() {}

  private checkFormValid() {
    if ((this._foodForm?.Name?.length ?? 0) > 0) {
      this._isFormValid = false;
    }
    this._isFormValid = true;
  }

  willUpdate(changedProperties: PropertyValues<this>) {
    if (changedProperties.has('food')) {
      this._foodForm = structuredClone(this.food);
      this._type = this.food && isDish(this.food) ? 'dish' : 'food';
    }
  }

  private NotifyChange() {
    this.dispatchEvent(UpEvent('food-changed', this._foodForm));
  }

  TypeChanged(e: CustomEvent) {
    e.stopPropagation();
    this._type = e.detail;
    if (this._type == 'dish') {
      this._foodForm = Object.assign(new Dish(), {
        Name: this._foodForm?.Name,
        Diem: this._foodForm?.Diem,
      });
    } else {
      this._foodForm = Object.assign(new FoodItem(), {
        Name: this._foodForm?.Name,
        Diem: this._foodForm?.Diem,
      });
    }
  }

  FoodItemChanged = (key: keyof FoodItem) => (e: CustomEvent) => {
    e.stopPropagation();
    this._foodForm = { ...this._foodForm, [key]: e.detail } as FoodItem;
    this.checkFormValid();
  };

  FoodChanged = (key: keyof Food) => (e: CustomEvent) => {
    e.stopPropagation();
    this._foodForm = { ...this._foodForm, [key]: e.detail } as Food;
    this.checkFormValid();
    if (this.fullForm) {
      return;
    }
    this.SaveFood();
  };

  DishChanged = (e: CustomEvent) => (key: keyof Dish) => {
    e.stopPropagation();
    this._foodForm = { ...this._foodForm, [key]: e.detail } as Dish;
    this.checkFormValid();
  };

  MacroChanged(e: CustomEvent) {
    this._foodForm = { ...this._foodForm, ...e.detail } as FoodItem;
  }

  async SaveFood() {
    if (!this._foodForm) {
      return;
    }
    this.NotifyChange();
  }

  render() {
    const titleForm = html` <food-title-form
      @food-name-changed="${this.FoodChanged('Name')}"
      @portion-changed="${this.FoodItemChanged('Portion')}"
      .food=${this._foodForm}
      .showEditButton=${!this.creatingNew}
      .isEditing=${this.fullForm}
    ></food-title-form>`;

    const typeForm = this.creatingNew
      ? html`<food-type-form
          @type-changed="${this.TypeChanged}"
          type=${this._type}
        ></food-type-form>`
      : nothing;

    const diemDailyForm = html`
      <span>Diem</span>
      <quantity-form
        @quantity-changed=${this.FoodChanged('Diem')}
        .quantity=${this._foodForm?.Diem ?? 0}
      ></quantity-form>
      <span>Today</span>
      <quantity-form
        @quantity-changed=${this.FoodChanged('Today')}
        .quantity=${this._foodForm?.Today ?? 0}
      ></quantity-form>
    `;

    const fullForm = html`
      ${typeForm}
      ${this._type == 'dish'
        ? html` <dish-form .food=${this._foodForm as Dish}></dish-form>`
        : html` <food-item-form
            @macro-changed="${this.MacroChanged}"
            .food=${this._foodForm as FoodItem}
          ></food-item-form>`}
    `;

    const saveFooter = html` <div class="footer-actions">
      <sl-button
        @click="${this.SaveFood}"
        .disabled=${!this._isFormValid}
        id="save"
        size="large"
        >Save</sl-button
      >
    </div>`;

    const displayForm = this.fullForm ? fullForm : nothing;

    return html`
      <form class="input-validation-required" action="#">
        <div class="form-body">
          ${titleForm} ${displayForm} ${diemDailyForm}
        </div>
        ${this.fullForm ? saveFooter : nothing}
      </form>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'food-form': FoodFormComponent;
  }
}
