export function resolve(obj: any, s: string, seperator = '.') {
  s = s.replace(/\[(\w+)\]/g, '.$1'); // convert indexes to properties
  s = s.replace(/^\./, ''); // strip a leading dot
  let a = s.split(seperator);
  for (let i = 0, n = a.length; i < n; ++i) {
    let k: string = a[i];
    if (k in obj) {
      obj = obj[k];
    } else {
      return;
    }
  }
  return obj;
}
