// goal ranges from -20 to 20

import { DietGoals } from '@models/diet/diet-goals';
import { ActivityLevelValues, GenderValues, WeightGoalValues } from '@models/diet/types';
import { PersonalInfo } from '@models/personal-info';

export function CalculatePersonal(info: PersonalInfo, goals: DietGoals) {
  function bmr() {
    return Math.round(
      10 * info.Weight + 6.25 * info.Height - 5 * info.Age + GenderValues.get(info.Gender)!
    );
  }
  function tdee() {
    return Math.round(bmr() * ActivityLevelValues.get(goals.ActivityLevel)!);
  }
  function calories() {
    return Math.round(tdee() * (1 + WeightGoalValues.get(goals.WeightGoal)! / 100));
  }
  // function lostweight() {
    // return goal <= 0
    //   ? '-' +
    //       Math.abs(
    //         this.precise((7 * (this.tdee - this.calories)) / 3500 / 2.20462, 2)
    //       )
    //   : '+' +
    //       Math.abs(
    //         this.precise((7 * (this.tdee - this.calories)) / 3500 / 2.20462, 2)
    //       );
  // }
  function protein() {
    return Math.round(2.20462 * info.Weight * 1);
  }
  function fat() {
    return Math.round(((calories() - 4 * protein()) * goals.FatCarbSplit) / 900);
  }
  function carbs() {
    return Math.round(((calories ()- 4 * protein()) * (100 - goals.FatCarbSplit)) / 400);
  }

  return {
    Calories: calories(),
    Protein: protein(),
    Carbs: carbs(),
    Fat: fat()
  }
}
