import { LitElement, html } from 'lit';
import { customElement, query } from 'lit/decorators.js';

import './diet-profile-form.component';
import './personal-profile-form.component';
import '@shoelace-style/shoelace/dist/components/details/details.js';
import SlDetails from '@shoelace-style/shoelace/dist/components/details/details.js';

@customElement('diet-profile')
export class DietProfileComponent extends LitElement {

  @query('#personal') personalDetails: SlDetails;
  @query('#diet') dietDetails: SlDetails;

  render() {
    return html`
      <sl-details
      @sl-show="${(_:any) => this.dietDetails?.hide()}"
      summary="Personal Profile" id="personal">
        <personal-profile-form></personal-profile-form>
      </sl-details>
      <sl-details
      @sl-show="${(_:any) => this.personalDetails?.hide()}"
      summary="Diet Profile" id="diet">
        <diet-profile-form></diet-profile-form>
      </sl-details>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    "diet-profile": DietProfileComponent;
  }
}